<template>
  <div class="selection-wallet-container page-container">
    <h3 class="text-center">Wallets</h3>
    <div class="button-container">
      <button
        v-touch:tap="touchMobbyTokenWallet"
        class="mid-round-button-blue-filled"
      >
        Mobby
      </button>
      <button
        v-touch:tap="touchEthWallet"
        class="mid-round-button-green-filled"
      >
        Ethereum
      </button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    touchEthWallet() {
      this.$store.dispatch('setWalletNamespace', 'eth');
      this.$router.push('/wallets/eth');
    },
    touchMobbyTokenWallet() {
      this.$store.dispatch('setWalletNamespace', 'mobby');
      this.$router.push('/wallets/mobby');
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'SelectionWalletContainer.scss';
</style>
